<template>
  <div class="bottom-menu">
    <div class="bottom-menu__items">
      <div class="bottom-menu__item">Информация
        <ul>
          <li @click="route('/main')">Главная</li>
          <li @click="route('/about')">О компании</li>
          <li @click="route('/articles')">Статьи</li>
          <li @click="route('/quest')">Вопросы и ответы</li>
          <li @click="route('/contacts')">Контакты</li>
        </ul>
      </div>
      <div class="bottom-menu__item">Продукты
