          <li @click="route('/product/0')">МЕГАМИКС-СЕМЕНА</li>
          <li @click="route('/product/1')">МЕГАМИКС-ПРОФИ</li>
          <li @click="route('/product/2')">МЕГАМИКС-БОР</li>
          <li @click="route('/product/3')">МЕГАМИКС-МАГНИЙ-ЦИНК</li>
          <li @click="route('/product/4')">МЕГАМИКС-СЕРА</li>
          <li @click="route('/product/5')">МЕГАМИКС-АЗОТ</li>
          <li @click="route('/product/6')">МЕГАМИКС-ФОСФОР</li>
          <li @click="route('/product/7')">МЕГАМИКС-КАЛИЙ</li>
          <li>Инструкция по применению</li>
        </ul>
      </div>
      <div class="bottom-menu__item">Культуры
        <ul>
          <li @click="route('/culture/0')">Зерновые</li>
          <li @click="route('/culture/1')">Подсолнечник</li>
          <li @click="route('/culture/2')">Кукуруза</li>
          <li @click="route('/culture/3')">Сахарная свекла</li>
          <li @click="route('/culture/4')">Картофель</li>
          <li @click="route('/culture/5')">Зернобобовые</li>
          <li @click="route('/culture/6')">Рапс</li>
          <li @click="route('/culture/7')">Лен</li>
          <li>Регламент применения</li>
        </ul>
      </div>
