        <ul>
          <li style="list-style-type: none;">Свидетельствоо госрегистрации</li>
          <li style="list-style-type: none;">Сертификат соответствия</li>
          <li style="list-style-type: none;">Декларация о соответствии</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BottomMenu',
  methods:{
    route(val){
          this.$router.push(val)

    }
  }
}
</script>

<style>
.bottom-menu{
  width: 100%;
  height: 275px;
  background: rgb(59, 155, 59);
  background-image: url(/static/img/footer.png);
  font-weight: 300;
}
.bottom-menu__items{
  position: relative;
  display: inline-block;
  width: 69%;
  height: 100%;
  margin-top: 20px;
  
}
.bottom-menu__item{
  position: relative;
  float: left;
  top: 0;
  width: 24%;
  color: #ffffff;
  font-size: 14px;
  text-align:left;
  text-decoration: none;
}
router-link{
  text-decoration: none;
}
</style>



