 повышается морозоустойчивость и зимостойкость. Значительно снижается риск выпревания, поражение снежной плесенью и корневыми гнилями. ")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"culture__video"},[_c('div',{staticClass:"culture__video-block"},[_c('img',{staticClass:"culture__video-img",attrs:{"src":"/static/cultures/RyeInfo.png","alt":""}})]),_vm._v(" "),_c('div',{staticClass:"culture__video-block"},[_c('div',{staticClass:"par-ttl"},[_vm._v("Рекомендации")]),_vm._v("\n        Огромное значение имеет применение некорневых подкормок, которые важно сделать в конец кущения, флаг. лист – колошение и налив зерна. Каждая обработка требует осознанного выбора препарата: при оптимальном корневом питании (внесение удобрений и наличие влаги) — «МЕГАМИКС – ПРОФИ», при ослаблении корневого питания — «МЕГАМИКС – АЗОТ» и «МЕГАМИКС – КАЛИЙ», при высокой плановой урожайности, насыщенности севооборота злаками «МЕГАМИКС – ЦИНК» в сочетании с другими удобрениями МЕГАМИКС. В случае если не применялся препарат «МЕГАМИКС – СЕМЕНА», а сев был сухим ли бо поздним, то можно стимулировать развитие всходов некорневыми подкормками «МЕГАМИКС – ПРОФИ». А на озимых при коротких сроках тепла повысить зимостойкость препаратом «МЕГАМИКС – КАЛИЙ».\n      ")])])}]
