<template>
  <div class="header-menu2">
      <div class="header-menu2__collum">
          <img src="@/assets/logo.png" alt="" class="header-menu__logo-img">
      </div>
      <div class="header-menu2__collum">
          <div class="header-menu">
              <div class="header-menu__item" v-for="(item, ind) of menuItems" :style = '{"font-weight": item.font}' :key="item" @mouseenter ="showEl(ind)" @mouseout="hideEl(ind)" @click="routerTo(item.router, ind)">{{item.name}}</div>
          </div>
      </div>
      <div class="header-menu2__collum">
          <div class="header-menu2__phone">+7 (831) 592 73 52</div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'HeaderMenu2',
  data () {
    return {
        menuItems: [
            {
                name: 'О компании',
                font: 100,
                items: ['string1','string2','string3'],
                show: false,
                router: '/About'
            },
            {
                name: 'Продукция',
                font: 100,
                items: ['string11','string2','string3'],
                show: false,
                router: '/Products'
            },
            {
                name: 'Культуры',
                font: 100,
                items: ['string12','string2','string3'],
                show: false,
                router: '/Cultures'
            },
            {
                name: 'Статьи',
                font: 100,
                items: ['string13','string2','string3'],
                show: false,
                router: '/Articles'
            },
            {
                name: 'Документация',
                font: 100,
                items: ['string14','string2','string3'],
                show: false
            },
            {
                name: 'Контакты',
                font: 100,
                items: ['string15','string2','string3'],
                show: false
            }
        ],
        index: 0
    }
  },
  methods:{
      showEl(value, index){
          this.index = value
          this.menuItems[value].show = true;
      },
      hideEl(value){
          this.index = value
          this.menuItems[value].show = false;
      },
      routerTo(val, index){
          this.menuItems.forEach(element => {
              element.font= 100
          });
          this.$router.push(val)
          this.menuItems[index].font = 400
      }
  }
}
</script>


<style>


   .header-menu2{
       display: flex;
       margin: 25px 0;
   }
   .header-menu2__collum{
       flex: 1;
   } 
   .header-menu2__collum:nth-of-type(2){
       flex: 3;
   }
   .header-menu{
       display: flex;
       margin-top: 15px;
   }
   .header-menu__item{
       flex: 1;
       letter-spacing: 0;
       color: #4D4F51;
       font-size: 18px;
       font-weight: 300;
   }
   .header-menu__item:hover{
       cursor: pointer;
       font-weight: 400;
   } 
   .header-menu__logo-img{
       width: 245px;
       height: 45px;
       margin-left: 60px;
   }
   .header-menu2__phone{
       margin-top: 12px;
       font-size: 21px;
       color: #4f4f51;
       font-weight: medium;
   }
</style>


