<template>
  <div class="culture">
    <div class="culture-info">
      <div class="main__block2">
        <div class="main__block-title left">МЕГАМИКС - СЕМЕНА</div>
        <div class="culture__block">
            <div class="culture__block-info">
                <img class="culture_block-img" src="/static/cultures/Rye1.png" alt="">
                <div class="culture__block-info-discription">
                    <div class="par">Предпосевная обработка удобрением МЕГАМИКС снижает стрессовое воздействие пестицидов, повышает полевую всхожесть на 10–15% и кущение на 25–45%, увеличивает устойчивость к полеганию, повышает иммунитет, засухоустойчивость, увеличивает потенциал культуры по урожайности и качеству. Предпосевная обработка семян способствует повышению зимостойкости растений за счет увеличения корневой системы и накопления низкомолекулярных метаболитов: </div>
                    <div class="par">Увеличение объема корней и их количества происходит из-за перераспределения трафика питательных веществ. Что является следствием действия МЕГАМИКС для озимых культур, когда преимущественно стимулируются процессы, направленные на развитие корневой системы. Мощные и длинные корни, с большим количеством корневых волосков, механически удерживают растение от выпирания, и повышают эффективность вносимых в почву основных удобрений. </div>
                    <div class="par">Одновременно с развитием корней стимулируется фотосинтез и выработка сахаров. При этом нарастание вегетативной массы культуры остается в норме, что способствует оттоку и накоплению в узле кущения сахаров, аминокислот, водорастворимых белков, гормонов, ненасыщенных жирных кислот. Накопление метаболитов в узле кущения понижает температуру кристаллизации воды в клетках, что помогает растению переносить заморозки.  </div>
                    <div class="par">В результате у озимых зерновых повышается морозоустойчивость и зимостойкость. Значительно снижается риск выпревания, поражение снежной плесенью и корневыми гнилями. </div>
                </div>
            </div>
        </div>            
      </div>
    
      <div class="culture__menu">
          <div class="culture__menu-title">Другие продукты</div>
          <div class="culture__menu-item" v-for="item of veget" :key="item.id">
              <div class="culture__menu-item-img"> <img :src="item.img" alt="" class="culture-img"> </div>
              <div class="culture__menu-item-name">{{item.name}}</div>
          </div>
      </div>
    </div>

    <br>

      <div class="culture__video">
        <div class="culture__video-block">
            <img src="/static/cultures/RyeInfo.png" alt="" class="culture__video-img">
        </div>

        <div class="culture__video-block">
          <div class="par-ttl">Рекомендации</div>
          Огромное значение имеет применение некорневых подкормок, которые важно сделать в конец кущения, флаг. лист – колошение и налив зерна. Каждая обработка требует осознанного выбора препарата: при оптимальном корневом питании (внесение удобрений и наличие влаги) — «МЕГАМИКС – ПРОФИ», при ослаблении корневого питания — «МЕГАМИКС – АЗОТ» и «МЕГАМИКС – КАЛИЙ», при высокой плановой урожайности, насыщенности севооборота злаками «МЕГАМИКС – ЦИНК» в сочетании с другими удобрениями МЕГАМИКС. В случае если не применялся препарат «МЕГАМИКС – СЕМЕНА», а сев был сухим ли бо поздним, то можно стимулировать развитие всходов некорневыми подкормками «МЕГАМИКС – ПРОФИ». А на озимых при коротких сроках тепла повысить зимостойкость препаратом «МЕГАМИКС – КАЛИЙ».
        </div>          

      </div>  
  </div>
</template>

<script>
export default {
  name: 'Culture',
  components: {

  },
  data () {
    return {
      veget: [
        {
          id: 0,
          name: 'Зерновые',
          img: 'static/cultures/Rye.png'
        },
        {
          id: 1,
          name: 'Подсолнечник',
          img: 'static/cultures/sunflower.png'
        },
        {
          id: 2,
          name: 'Кукуруза',
          img: 'static/cultures/corn.png'
        },
        {
          id: 3,
          name: 'Сахарная свекла',
          img: 'static/cultures/shugars.png'
        },
        {
          id: 4,
          name: 'Картофель',
          img: 'static/cultures/pottatoo.png'
        },
        {
          id: 5,
          name: 'Зернобобовые',
          img: 'static/cultures/bobs.png'
        },
        {
          id: 6,
          name: 'Рапс',
          img: 'static/cultures/raps.png'
        },
        {
          id: 7,
          name: 'Лён',
          img: 'static/cultures/len.png'
        },
      ]
    }
  },
}
</script>

<style>
.culture{
    padding: 0 5%;
}
.culture_block-img{
  margin-bottom: 20px;
  width: 100%;
}
.culture-info{
  position: relative;
  width: 100%;
  position: relative;
  vertical-align: top;
  display: inline-flex;
}
.main__block2{
    width: 66%;
    display: inline-block;
}
.culture__menu{
    width: 33%;
    display: inline-block;
    text-align: left;
    padding: 15px 0 0 60px;
}
  .culture__menu-item-img{
      display: inline-block;
  }
  .culture__menu-item-name{
      display: inline-block;
  }
  .culture__menu-title{
    margin-bottom: 20px;
  }
  .culture__block-info-discription{
      text-align: justify;
      font-size: 16px;
  }
  .culture-img{
      width: 70px;
      height: 70px;
      margin-bottom: 20px;
  }
  .tbl-elem{
      width: 100%;
      margin: 30px 0 30px 0;
  }
  .par-ttl{
      font-size: 24px;
      margin-bottom: 20px;
  }
  .par{
    margin-bottom: 20px;
  }
  .culture__video{
    position: relative;
    width: 100%;
    display: inline-flex;
    margin: 60px 0;
  }
  .culture__video-block{
    width: 50%;
    text-align: left;
  }
  .culture__video-img{
      width: 95%;
  }
  .culture__video-title{
    margin-bottom: 20px;
  }
</style>



